import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartDataSets } from 'chart.js';
import { Label, Color } from 'ng2-charts';
import { HttpClient } from '@angular/common/http';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { InventariosService } from '../../../providers/Api/Inventarios.service';
import { ActivatedRoute } from '@angular/router';
import { IonInfiniteScroll } from '@ionic/angular';

@Component({
  selector: 'app-ivt-estadisticas',
  templateUrl: './ivt-estadisticas.page.html',
  styleUrls: ['./ivt-estadisticas.page.scss'],
})
export class IvtEstadisticasPage implements OnInit {

  chartData: ChartDataSets[] = [{ data: [], label: '' }];
  chartLabels: Label[];


  chartData2: ChartDataSets[] = [{ data: [], label: '' }];
  chartLabels2: Label[];

  // Options
  chartOptions = {
    responsive: true,
    title: {
      display: true,
      text: 'Total de Movimientos en inventario'
    },
    zoom: {
      enabled: true,
      mode: 'xy'
    },
  };
  chartColors: Color[] = [
    {
      borderColor: '#000000',
      backgroundColor: ['#FD8A24', '#333333']
    }
  ];
  chartType = 'pie';
  showLegend = true;

  chartColors2: Color[] = [
    {
      borderColor: '#000000',
      backgroundColor:'#FAD60F'
    }
  ];

  chartOptions2 = {
    responsive: true,
    title: {
      display: true,
      text: 'Productos con más salidas'
    },
    zoom: {
      enabled: true,
      mode: 'xy'
    },
    scales: {
      xAxes: [{
        gridLines: {
          color: 'rgba(171,171,171,1)',
          lineWidth: 1
        }
      }],
      yAxes: [{
        ticks: {
          beginAtZero: true,
          min: 0
        }
      }]
    }
  };
  chartType2 = 'bar';
  showLegend2 = true;


  cierres = [];
  actual = {
    desde: '',
    hasta: ''
  };
  entradas = 0;
  salidas = 0;

  graficaProductos = [];

  periodoActual = 'Periodo 1';

  vista = 'general';

  productos = [];
  load = false;

  patron = '';
  desde = 0;
  @ViewChild('inifinito', {static: false}) infinite: IonInfiniteScroll;

  constructor(
    private http: HttpClient,
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private ivtApi: InventariosService,
    private activeRoute: ActivatedRoute
    ) {
  }


  ionViewWillEnter() {

      this.recargar();
  }

  async recargar() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.ivtApi.CierresInventario(info.token, {ivt: this.activeRoute.snapshot.paramMap.get('id')}).subscribe((data: any) => {

          console.log(data, 'Data');

          if (data.exe) {

            if (data.response.length > 0) {
              data.response.forEach((item, idx) => {
                  item.periodo = 'Periodo ' + (idx + 1);
              });
              this.cierres = data.response;
              console.log(data.response);
              this.actual.desde = this.cierres[0].fechadesde;
              this.actual.hasta = this.cierres[0].fechahasta;
              this.traerMovimientos(this.cierres[0].fechadesde, this.cierres[0].fechahasta);
              this.traerMovimientos2(this.cierres[0].fechadesde, this.cierres[0].fechahasta);
            }

            console.log(this.cierres, 'cierres');

          //  this.cierres = data.response;
          } else {
            this.toast.toastError('No se pudo traer los cierres de inventario, inténtelo más tarde');
          }

        }, (err) => {
          this.toast.toastError('No se pudo traer los cierres de inventario, inténtelo más tarde');
        });
    }
  }


  async traerMovimientos(desde, hasta) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.ivtApi.MovCantidades(info.token, {ivt: this.activeRoute.snapshot.paramMap.get('id'), desde, hasta}).subscribe((data: any) => {
          console.log(data, 'Data');

          if (data.exe) {


            if (data.paso) {

              this.entradas = data.entradas;

            } else {
              this.entradas = data.entradas;
              this.salidas = data.salidas;
            }

            this.recargarGraficas();

          //  this.cierres = data.response;
          } else {
            this.toast.toastError('No se pudo traer los movimientos del inventario, inténtelo más tarde');
          }

        }, (err) => {
          this.toast.toastError('No se pudo traer los movimientos del inventario, inténtelo más tarde');
        });
    }
  }


  async traerMovimientos2(desde, hasta) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.ivtApi.MovCantidades2(info.token, {ivt: this.activeRoute.snapshot.paramMap.get('id'), desde, hasta}).subscribe((data: any) => {
          console.log(data, 'Data agrupada');

          if (data.exe) {

            this.graficaProductos = [];

            if (data.response.length > 0) {
              data.response.forEach((item) => {
                this.graficaProductos.push({producto: item._id.nombre, cantidad: item.total});
              });

              const productos = this.graficaProductos.map((item) => {
                const nombre = item.producto + ' - '  + item.cantidad;
                return nombre;
              });
              const cantidades = this.graficaProductos.map((item) => {
                return  item.cantidad;
              });
              this.chartLabels2 = productos;
              this.chartData2[0].data = cantidades;
            }

          //  this.cierres = data.response;
          } else {
            this.toast.toastError('No se pudo traer los movimientos del inventario, inténtelo más tarde');
          }

        }, (err) => {
          this.toast.toastError('No se pudo traer los movimientos del inventario, inténtelo más tarde');
        });
    }
  }

  cambioPeriodo(event) {
      this.periodoActual = event.detail.value;

      const fechas = this.cierres.filter((item) => item.periodo === event.detail.value);

      if (fechas.length > 0) {
        this.traerMovimientos(fechas[0].fechadesde, fechas[0].fechahasta);
        this.traerMovimientos2(fechas[0].fechadesde, fechas[0].fechahasta);
        this.actual.desde = fechas[0].fechadesde;
              this.actual.hasta = fechas[0].fechahasta;
      }
  }

  recargarGraficas() {
    this.chartLabels = ['Entradas  - ' + this.entradas, 'Salidas  - ' + this.salidas];
    this.chartData[0].data = [this.entradas, this.salidas];

    if (this.graficaProductos.length > 0) {
       const productos = this.graficaProductos.map((item) => {
        const nombre = item.producto + ' - '  + item.cantidad;
        return nombre;
      });
       const cantidades = this.graficaProductos.map((item) => {
        return  item.cantidad;
      });
       this.chartLabels2 = productos;
       this.chartData2[0].data = cantidades;
    }
  }

  segmentChanged( event ) {

    this.vista = event.detail.value;
    if ( event.detail.value === 'productos') {
      this.cargarProductos();
    } else {
      this.load = false;
      this.productos = [];
      this.desde = 0;
    }

  }
  ngOnInit() {
  }

  async cargarProductos() {
    this.desde = 0;
    if (this.load) {
      
      this.infinite.disabled = false;
    }
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.load = true;
      this.ivtApi.leerProductoEstadistica(info.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id'), desdef: this.actual.desde, hasta: this.actual.hasta } ).subscribe((data: any) => {
        console.log(data, 'Productos');
        if (data.exe) {
            this.productos = data.response;
        } else {
          this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        }
        this.load = true;
      }, (err) => {
          this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
      })
    }
}


  async buscar(event) {
    console.log(event.detail, 'epale');
    
    if ( event.detail.value !== '') {
      this.desde = 0;
      this.infinite.disabled = false;
      this.patron = event.detail.value;

      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.leerProductoEstadistica(info.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id'), desdef: this.actual.desde, hasta: this.actual.hasta, patron: this.patron }).subscribe((data: any) => {
          console.log(data, 'mi data');
          if ( data.exe ){
            this.productos = data.response;
            if ( data.response.length === 0) {
              this.desde = 0;
            }
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
    } else {
      this.desde = 0;
      
      console.log(this.infinite.disabled)
      this.cargarProductos();
    }
  }

  async loadData( event ) {
    // event.target.complete()
    this.desde = this.desde + 5;

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      if ( this.patron === '') {
        this.ivtApi.leerProductoEstadistica(info.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id'), desdef: this.actual.desde, hasta: this.actual.hasta }).subscribe((data: any) => {
          console.log(data);
          if (data.exe) {
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.productos.push(hoja);
              })
              event.target.complete();
             } else {
              event.target.disabled = true;
              this.desde = 0;
             }
          } else {
            this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
          }
        }, (err) => {
          this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
        })
      } else {
        // tslint:disable-next-line:max-line-length
        console.log(this.desde, 'pli');
        this.ivtApi.leerProductoEstadistica(info.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id'), desdef: this.actual.desde, hasta: this.actual.hasta }).subscribe((data: any) => {
          console.log(data, 'mi data 2', this.desde);
          if ( data.exe ){
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.productos.push(hoja);
              })
              event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
      
      
    }
    }
    

}
